import React from 'react';
import '../css/Main.css';
import { useNavigate } from 'react-router-dom';
import WordCard from './WordCard.js';
import AddBtn from './AddBtn';
import {loadDictionFB, addDictionFB} from "../redux/modules/diction";
import { useDispatch } from 'react-redux'; // redux  hook

function Main(props) {
    const navegate = useNavigate();
    const dispatch = useDispatch();

    // main firebase db    loadDictionFB(), addDictionFB()  .
    React.useEffect(()=>{
        dispatch(loadDictionFB(), addDictionFB());
      }, []);

    return (
        <div className="main">
            <div className='wrap'>
                <div className='div_1'>
                    <WordCard />
                </div>
                <div className='div_2' onClick={()=>{navegate("/update/add");}}>
                    <AddBtn/>
                </div>
            </div>
        </div>
    );
}

export default Main;
