// diction.js
import { db } from "../../firebase"; // firebase db .
import { collection, getDoc, getDocs, addDoc, updateDoc, doc, deleteDoc } from "firebase/firestore"; // firebase  
import { async } from "@firebase/util";

// Actions( )
const LOAD = 'diction/LOAD'; // 
const CREATE = 'diction/CREATE'; // 
const UPDATE = 'diction/UPDATE'; // 
const REMOVE = 'diction/REMOVE'; // 

//  ( ..)
const initalState = {
    list: [

    ]
}

// Action Creators (     )
export function loadDiction(diction_list) {
    return { type: LOAD, diction_list: diction_list };
}

export function createDiction(diction) { // create    
    return { type: CREATE, diction: diction }; // {, }
}

export function updateDiction(diction) {
    return { type: UPDATE, diction: diction };
}

export function removeDiction(diction) {
    return { type: REMOVE, diction: diction };
}

// middelware

// firebase   
export const loadDictionFB = () => { //    .

    return async function (dispatch) { //   redux  dispatch  .    .
        const diction_data = await getDocs(collection(db, "dictionary")); // dictionary_data getDocs     .
        console.log(diction_data); //       .   ...

        let diction_list = []; // 1. dictionary_list   

        diction_data.forEach((doc) => { //2.    
            console.log(doc.data());
            diction_list.push({ ...doc.data() }); // doc.data    .
        }); // getDocsfh     dictionary_data forEach()   
        console.log(diction_list)

        dispatch(loadDiction(diction_list)) //loadDiction  ("  !  ").
        //        .
    }
}

// firebase   
export const addDictionFB = (new_word) => {
    return async function (dispatch) {
        const docRef = await addDoc(collection(db, "dictionary"), new_word); //  :   firebase  /  :   
        // const _diction = await getDoc(docRef);
        const diction = { id: docRef.id, ...new_word }

        console.log(diction);

        // dispatch(createDiction(diction)) //createDiction  ("  !  ").
        //        .
    }
}

// Reducer
export default function reducer(state = initalState, action = {}) { // state = {} :  
    switch (action.type) {
        case "diction/LOAD": {
            return { list: action.diction_list }; //   diction_list  
        }
        case "diction/CREATE": {
            const new_diction_list = [...state.list, action.diction];
            return { list: new_diction_list }; //          .
        } // 
        default: return state;
    }
}

