//store Reducer  rootReducer .

import {createStore, combineReducers} from "redux";
import {applyMiddleware, compose} from "redux"; //    import 
import diction from "./modules/diction.js";
import thunk from "redux-thunk" //     thunk import

//   
const rootMiddleware = [thunk];
const enhancer = applyMiddleware(...rootMiddleware) //     

//    (  ..)
const rootReducer = combineReducers({diction});

const store = createStore(rootReducer, enhancer) //    (+)   .   !!

export default store;